/*fonts*/
@import url("https://fonts.googleapis.com/css2?family=Domine&family=Mulish&display=swap");
/*colors*/
h1 {
  font-family: Mulish, serif; }

p {
  text-align: left;
  color: black;
  font-size: 15pt;
  font-family: Domine, sans-serif;
  line-height: 30px; }

a {
  color: blue;
  font-weight: bolder;
  font-family: Mulish; }

strong {
  font-family: Mulish;
  font-size: 20pt; }

li {
  font-family: Domine;
  font-size: 15pt; }

#banner {
  width: 100%;
  padding-bottom: 10px; }

#name {
  text-align: center;
  padding: 10px;
  margin: 0; }
  #name a {
    font-family: Mulish, serif;
    text-decoration: none;
    color: black; }

nav {
  width: 100%;
  background: black;
  float: right;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: space-around;
  margin-bottom: 20px; }
  nav a {
    width: 33%;
    text-align: center;
    padding: 15px;
    text-decoration: none;
    color: white; }

#profile-holder {
  width: 100%;
  text-align: center;
  height: auto; }

#profile-pic {
  width: 40%; }

#profile-text {
  visibility: hidden;
  height: 0; }

#greeting {
  text-align: center; }

#body {
  background-color: white;
  width: 80%;
  margin: auto;
  padding-bottom: 90px; }

#footer {
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  flex-flow: row;
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: black;
  overflow-y: hidden; }
  #footer a {
    text-decoration: none;
    font-weight: bolder;
    font-size: 8pt;
    line-height: 50px;
    color: white; }
  #footer span {
    line-height: 50px;
    text-align: center; }

#steadfast-gif {
  width: 100%; }

#linkedin-icon {
  padding: 10px;
  filter: grayscale(100%);
  height: 30px; }

#github-icon {
  filter: invert(100%);
  padding: 10px;
  height: 30px; }

@media only screen and (min-width: 598px) {
  nav {
    height: 70px; }
    nav a {
      line-height: 70px;
      padding: 0px;
      font-size: 15pt; }
  #profile-holder {
    width: 30%;
    height: 488px;
    float: right; }
  #profile-text {
    visibility: visible;
    height: auto; }
    #profile-text p {
      text-align: center;
      font-style: italic; }
  #footer {
    height: 90px; }
    #footer a {
      font-size: 10pt;
      line-height: 90px; }
    #footer span {
      text-align: center; }
  #steadfast-gif {
    width: 50%; }
  #linkedin-icon {
    padding: 20px;
    height: 50px;
    filter: grayscale(100%);
    float: right; }
  #github-icon {
    padding: 20px;
    height: 50px;
    filter: invert(100%);
    float: left; } }

